.profile{
    display: inline-block;
    background-color: #fff;
    border-radius: 6px;
    width: 100%;
}
.profile_name{
    line-height: 18px;
    padding: 20px;
}
.profile_name .name{
    text-transform: uppercase;
    font-size: 17px;
    font-weight: bold;
}
.profile_name .title{
    font-size: 13px;
    color:#777;
}
.profile_image{
    margin-top: -10px;
    -webkit-clip-path: polygon(0 9%, 100% 0,100% 94%, 0% 100%);
    clip-path: polygon(0 9%, 100% 0,100% 94%, 0% 100%);
}
.profile_image img{
    width: 100%;
}

/* Informaton */
.profile_information{
    top: 0;
    left: 0;
    margin: -40px 10px 30px;
}
.profile_information .timeline .timeline_content{
    margin-top: 2px;
    margin-bottom: 12px;
}
.MuiTimelineItem-content > .timelineitem_text,
.MuiTimelineItem-content > .timelineitem_text> a{
    font-size: 13.5px;
    color: #787878;
    font-weight: 400;
    text-decoration: none;
}
.MuiTimelineItem-content > .timelineitem_text> a:hover{
    color: var(--main-color);
    text-decoration: underline;
}
.timelineitem_text > span{
    color: #000;
    font-weight: 500;
}
.profile .button_container{
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 20px;
}