 button.custom_btn,a.custom_btn{
    background-color: var(--main-color)!important;
    font-size: 24px;
    font-weight: 500;
    border: 0;
    border-radius: 50px;
    padding: 1px 12px;
    line-height: 35px;
    min-width: 100px;
    outline: none;
}
button.custom_btn:hover,a.custom_btn:hover{
    background-color: #fd7d67 !important;
    -webkit-box-shadow: 0 0 5px 2px rgba(182, 182, 182, 0.2);
    -moz-box-shadow: 0 0 5px 2px rgba(182, 182, 182, 0.2);
    box-shadow: 0 0 5px 2px rgba(182, 182, 182, 0.2);
}
.custom_btn .btn_icon_container{
    color: #000;
    background: #fff;
    border-radius: 50px;
    height: 27px;
    width: 27px;
    line-height: 29px;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}
.custom_btn .btn_icon_container > .MuiSvgIcon-root{
    font-size: 16px;
}
.custom_btn .btn_text{
    color: floralwhite;
    font-size: 14px;
    text-transform: none;
    text-align: center;
    width: 100%;
    margin-right: 5px;
    margin-left: 5px;
}
