.timeline{
    padding: 0 !important;
}
.timeline li:last-of-type .MuiTimelineConnector-root{
    display: none;
}
/*  Header */
.timeline .timeline_header{
    padding-top: 7px;
}
.timeline .timeline_firstItem .MuiTimelineConnector-root{
    min-height: 30px;
}
.timeline .MuiTimelineItem-root{
    min-height: 0px !important;
}
.timeline .MuiTimelineItem-missingOppositeContent:before{
    display: none;
}
.timeline .timeline_dot_header{
    color: #000;
    background-color: var(--main-color);
    font-size: small;
    padding: 10px;
}

/* Remaining items */
.timeline .timeline_dot{
    color: #000;
    border-color: var(--main-color);
    font-size: small;
    padding: 2px;
}
.timeline .timeline_dot_header> .MuiSvgIcon-root{
    font-size: 20px;
    color: #f9f6e3;
}
.timeline .separator_padding{
    padding-left: 18px;
}
.timeline .timeline_content{
    padding-top: 0;
    margin-bottom: 20px;
}