/* Tabs */

.customTabs .customTabs_item{
  padding: 0;
  min-width: 0;
  margin-right: 20px;
  text-transform: inherit;
  font-size: 14px;
  font-weight: 400;
  outline-width: 0;  
}
.customTabs .active{
    color: var(--main-color);
}

/* End of tabs */

/* Cards */
.MuiGrid-item >.customCard{
    height: 100%;
    max-width: 350px;
    box-shadow: 0 0 48px 0 rgba(4, 6, 4, 0.08);
    border-radius: 5px;
    overflow: hidden;
    outline-width: 0;
}
.customCard .customCard_image{
    height: 140px;
}
.customCard .customCard_title{
    font-weight: 500;
}
.card-btn{
    display: block;
    font-size: 13px;
    color: var(--main-color);
    font-weight: 400;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
}
.customCard .customCard_caption{
    color: #808080;
    font-weight: 400;
}

/* End of cards */

/* Dialog */

.projectDialog{
    display: flex;
    align-items: center;
    justify-content: center;
}
.projectDialog .projectDialog_image{
    width: 100%;
    object-fit: cover;
    object-position: center center;
}
.projectDialog .projectDialog_description {
    margin-top: 10px;
}
.projectDialog .projectDialog_actions{
    justify-content: center;
    margin-bottom: 10px;
}
.projectDialog .projectDialog_icon{
    color: darkslategray;
    margin: 0 12px;
    cursor: pointer;
}
.projectDialog .projectDialog_icon:hover{
    color: var(--main-color);
}
.projectDialog .projectModal_icon > .MuiSvgIcon-root{
    font-size: 30px;
}
.projectDialog .MuiDialogActions-root{
    padding: 0 15px 15px 15px;
}
.projectDialog .MuiPaper-root .MuiDialogContent-root{
    overflow-y: initial;
}
.projectDialog .MuiPaper-root .MuiDialogActions-root{
    justify-content: space-between;
}