/* About me section */
.aboutme_text{
    color: darkslategrey;
    font: smaller !important;
    line-height: 22px !important;
    white-space: pre-line;
}
/* End of about me section*/


/* Timeline section*/
.resume_timeline .MuiTimelineContent-root .timeline_title{
    font-size: 16px;
    font-weight: 500;
}
.resume_timeline .MuiTimelineContent-root .timeline_date{
    color: darkslategray;
}
.resume_timeline .MuiTimelineContent-root .timeline_description{
    color: gray;
    font-size: small;
    margin-top: 6px;
}
/*End of timeline section*/

/* Services */
.service{
    height: 100%;
    text-align: center;
}
.service .service_title{
    font-size: 16px;
    font-weight: 500;
    margin-bottom: 15px;
    text-align: center;
}
.service .service_description{
    color: #989898;
    border-color: var(--main-color);
    margin-right: 5px;
    text-align: center;
}
.service >.MuiIcon-root> .MuiSvgIcon-root{
    font-size: 46px;
    margin-left: 0;
    font-weight: 300;
    color: darkslategray;
}
/* End of services */

/* Skills */
.MuiGrid-spacing-xs-3 > .MuiGrid-item .skill{
    border-radius: 8px;
    padding: 20px;
    box-shadow: 0 0 48px 0 rgba(4, 6, 4 , 0.08);
    min-height: 186px;
    height: 100%;
}
.skill .skills_title{
    font-size: 14px;
    font-weight: 500;
    line-height: 35px;
}
.skill .skill_description{
    display: flex;
    align-items: center;
    color: #989898;
    font-weight: 400;
    line-height: 18px;
}
.skill .skill_description .timeline_dot{
    border-color: var(--main-color);
    padding: 2px;
    margin-right: 5px;
}
/* End of skills */

/* Contact Section */
.MuiFormControl-fullWidth .MuiFormLabel-root.Mui-focused{
    color: var(--main-color) !important;
}
.MuiFormControl-fullWidth .MuiInput-underline:after{
    border-bottom: 2px solid var(--main-color);
}
.MuiGrid-root .contactInfo_item{
    font-size: 14px;
    color: #787878;
    line-height: 34px;
}
.contactInfo_item > span{
    color: #000;
    font-weight: 500;
}
.contactInfo_socialsContainer{
    margin-top: 20px;
}
.contactInfo_social > a > .MuiSvgIcon-root{
    font-size: 16px;
    margin-right: 10px;
    color: #000;
}

/* End of contact Section */